import React, { useEffect, useState } from "react"
import "blip-toolkit/dist/blip-toolkit.css"
import { getApplication, getContacts, getThreads } from "./api/applicationService"
import { showToast, withLoading } from "./api/commomService"
import { PageHeader } from "components/PageHeader"
import { BlipTable } from "components/BlipTable"
import { CommonProvider } from "contexts/CommonContext"
import { PageTemplate } from "components/PageTemplate"
import { BlipTabs } from 'blip-toolkit'

import CloneBots from 'components/CloneBots/clone'
import MarketPlace from 'components/MarketPlace/marketplace'


const tableContactsModel = [
    { label: "Name", key: "name" },
    { label: "Group", key: "group" },
    { label: "Identity", key: "identity" },
    { label: "Source", key: "source" }
]

const tableThreadsModel = [
    { label: "Identity", key: "identity" },
    { label: "Last Message", key: "lastMessage" },
    { label: "Unread Messages", key: "unreadMessages" }
]

function App() {
    const [application, setApplication] = useState({shortName: 'Clone Bots'})
    const [contacts, setContacts] = useState([])
    const [threads, setThreads] = useState([])

    const fetchApi = async () => {
  
        showToast({
            type: "success",
            message: "Success loaded"
        })
    }

    useEffect(() => {
       
        withLoading(async () => {

        try {
            const {accessKey, shortName } = await getApplication();
            const KEY = 'Key ' + btoa(`${shortName}:`+ atob(accessKey))
           
             localStorage.setItem('keyTwo', KEY);
             localStorage.setItem('keyTarget', KEY);
             new BlipTabs('tab-nav')
             await fetchApi()

        }catch(e) {

             return false;
        }

        })
    }, [])

    const title = application.shortName

    return (
        <CommonProvider>
            <div id="main" className="App">
                <PageHeader title={title} />
                <PageTemplate title={title}>
                    <div id="tab-nav" className="bp-tabs-container">
                        <ul className="bp-tab-nav">
                            <li>
                                {/* eslint-disable-next-line */}
                                <a href="#" data-ref="clonebots">Clone bots</a>
                            </li>
                            <li>
                                {/* eslint-disable-next-line */}
                                <a href="#" data-ref="marketplace">MarketPlace</a>
                            </li>
                          
                        </ul>
                        <div className="bp-tab-content fl w-100" data-ref="clonebots">
                           <CloneBots/>
                        </div>
                        <div className="bp-tab-content fl w-100" data-ref="marketplace">
                            <MarketPlace/>
                        
                        </div>
                       
                    </div>
                </PageTemplate>
            </div>
        </CommonProvider>
    )
}

export default App